import React, { useCallback } from 'react';
import { useHistory } from 'react-router-dom';
import { Grid } from '@material-ui/core';
import { Breadcrumb, ButtonCustom, TitleCustom } from 'src/components/base';
import ItemInformation from 'src/components/base/layout/PromotionItem';

import './recruitment.scss';

const RecruitmentScreen = (props) => {
  const history = useHistory();

  const handleOnClickItem = useCallback((item) => {
    history.push(`/recruitment/${item.id}`, { item });
  }, []);
  console.log('props?.listRecruitment', props?.listRecruitment);
  return (
    <div className="recruitment-container">
      <Breadcrumb items={[{ name: 'Tuyển dụng', url: '/recruitment' }]} />

      {/* <Grid container lg={12} md={12} justifyContent="center">
        <Slider onClickSlider={() => {}} data={props.listBanner} />
      </Grid> */}

      <div className="recruitment_layout">
        <div
          style={{ marginTop: '20px' }}
          maxWidth="lg"
          className="recruitment_content"
        >
          <div style={{ marginBottom: '36px' }}>
            <TitleCustom title="Tuyển dụng" />
          </div>
          {props?.listRecruitment?.data?.map((obj, idx) => {
            const flagHiddenLine = props.listRecruitment.length - 1 === idx;
            return (
              <ItemInformation
                key={obj?.id}
                data={obj}
                hideLine={flagHiddenLine}
                onClickItem={handleOnClickItem}
              />
            );
          })}
          {props.listRecruitment?.data?.length <
            props.listRecruitment.total && (
            <Grid container className="box-button">
              <ButtonCustom
                onClick={props.onLoadMoreClick}
                item
                title="XEM THÊM"
                customStyle="custom-button"
              />
            </Grid>
          )}
        </div>
      </div>
    </div>
  );
};

export default React.memo(RecruitmentScreen);
