import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getListRecruitmentHandle } from 'src/redux/actions/recruitment.action';
import { getListBannerSelector } from 'src/redux/selectors/common.selector';
import RecruitmentView from './recruitment.view';

const RecruitmentContainer = () => {
  const [listRecruitment, setListRecruitment] = useState({});
  const dispatch = useDispatch();
  const listBanner = useSelector(getListBannerSelector);
  useEffect(() => {
    dispatch(
      getListRecruitmentHandle(1, 10, (listRecruitmentRes) => {
        setListRecruitment({
          ...listRecruitmentRes,
          total: listRecruitmentRes.metadata.total,
          page: 1,
          size: 10,
        });
      })
    );
  }, []);
  return (
    <RecruitmentView
      onLoadMoreClick={() => {
        dispatch(
          getListRecruitmentHandle(
            listRecruitment.page + 1,
            listRecruitment.size,
            (recruitment) => {
              setListRecruitment({
                ...recruitment,
                data: listRecruitment.data.concat(recruitment.data),
                page: listRecruitment.page + 1,
                size: listRecruitment.size,
                total: recruitment.metadata.total,
              });
            }
          )
        );
      }}
      listBanner={listBanner}
      listRecruitment={listRecruitment}
    />
  );
};

export default RecruitmentContainer;
